.App {
  font-family: sans-serif;
  text-align: center;
}

.card:hover {
  transform: scale(1.03);
  /*background-image: linear-gradient(to right, #ddd6f3, #faaca8);*/
  background-image: url("https://image.shutterstock.com/image-vector/business-pattern-background-260nw-790298002.jpg");
  box-shadow: 0 4px 30px 0 rgba(45, 54, 65, 0.75);
}

.card-title:hover {
  color: #005cb8;
  font-weight: 900;
}

.card-text:hover {
  color: #dd2476;
  font-weight: 900;
}

.btn-hover {
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align: center;
  border: none;
  background-size: 300% 100%;

  border-radius: 50px;
  moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  background-image: linear-gradient(
    to right,
    #f5ce62,
    #e43603,
    #fa7199,
    #e85a19
  );
  box-shadow: 0 4px 15px 0 rgba(229, 66, 10, 0.75);
}

.btn-hover:hover {
  background-position: 100% 0;
  moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

